

























































.toast-enter ,.toast-leave-to{
  transform: translateY(-100%);
  transition: all .3s ease;
  opacity: 0;
}
.toast-enter-to, .toast-leave{
  transform: translateY(0);
  transition: all .3s ease;
  opacity: 1;
}
.toast__mesage{
  position: absolute;
  right: 10px;
  top: 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0px 12px;
  width: 279px;
  height: 36px;
  background: #15A758;
  box-shadow: 0px 2px 24px rgba(0, 0, 0, 0.08);
  border-radius: 6px;
}
.toast__close{
  height: 20px;
  width: 20px;
  border-radius: 50%;
  background-color: #72c998;
  background-image: url(/src/assets/img/cancel.svg);
  background-position: -2px -2px;
  cursor: pointer;
}
#app{
  padding: 10px;
  min-height: 100%;
  width: 100%;
  background-color: rgb(128, 128, 128);

  &.app_phone{
    padding: 0;
    padding-top:1px;
  }
}
.button_show{
  width: initial;
  margin: 25px;
}
